
html,body{width:100%;height:100%;overflow:hidden;margin:0
}
h1,h2{font-weight:normal
}
ul{list-style-type:none;padding:0
}
li{display:inline-block;margin:0 10px
}
a{color:#42b983;font-size:18px
}
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#5d6063;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;margin-top:100px
}
#robot{width:200px;height:250px;margin:auto;padding:10px;position:relative;cursor:pointer;-webkit-tap-highlight-color:transparent
}
#robot i{font-style:normal
}
#robot .bubble{display:inline-block;position:absolute;border:2px dotted #6786e0;padding:3px 8px;border-radius:10px;font-size:14px;width:100px;left:170px;text-align:center;z-index:20;background-color:#fff
}
#robot .bubble:after{content:'';display:inline-block;border:2px dotted #6786e0;width:8px;border-top:0;border-right:0;height:7px;position:absolute;top:9px;background-color:#fff;left:-6px;transform:rotate(40deg)
}
#robot .head{border:2px solid #d0b8b8;margin:40px auto 0;height:50px;width:80px;border-radius:10px;position:relative;background-color:#f1f5f7
}
#robot .head .mast{display:inline-block;width:0;height:20px;border:2px dotted #3d9db5;position:relative;top:-25px
}
#robot .head .mast:after{content:'';display:block;width:6px;height:6px;border:1px solid #bba7ad;position:relative;top:-6px;left:-4px;background-color:#8bf939;border-radius:50%;animation:light-blink 3s infinite
}
@keyframes light-blink{
0%{background-color:#8bf939
}
50%{background-color:#ab7b8a
}
100%{background-color:#8bf939
}
}
#robot .head .eyes{display:inline-block;position:absolute;top:13px;transform:translateY(-50%);width:10px;height:10px;overflow:hidden;border:2px solid #dac3c3;border-radius:50%;animation:blink 3s infinite
}
#robot .head .eyes.left{left:20px
}
#robot .head .eyes.right{right:20px
}
#robot .head .eyes .dot{display:inline-block;width:0px;border:2px solid #000;position:absolute;border-radius:50%;top:3px;left:3px
}
#robot .head:hover .eyes{animation:none
}
@keyframes blink{
0%{height:10px
}
5%{height:0px
}
10%{height:10px
}
100%{height:10px
}
}
#robot .head .mouse{display:block;position:absolute;width:15px;left:50%;font-weight:bold;line-height:20px;transform:translateX(-50%)
}
#robot .body{border:2px solid #796a6a;background-color:#98d43a;width:100px;height:100px;border-radius:10px;margin:2px auto 0;position:relative;z-index:3;overflow:hidden
}
#robot .body .collar{display:block;width:50px;height:50px;transform:rotate(45deg);margin:-40px auto 30px;border:1px solid #000;background-color:#fff
}
#robot .body .fastener{display:block;width:5px;height:5px;border:1px solid #f0f0f0;margin:20px auto;background-color:#a28686;border-radius:50%
}
#robot .arms .arm{z-index:14;background-color:#98d43a;top:120px;width:60px;height:10px;display:inline-block;border:2px solid #796a6a;position:absolute;border-left:0
}
#robot .arms .arm:after{content:'';display:inline-block;width:10px;height:13px;border:2px solid #796a6a;position:absolute;right:-10px;border-radius:10px;top:-3px;background-color:#fff
}
#robot .arms .left{transform-origin:0 0;transform:translateX(-70%) scaleX(-1) rotate(45deg)
}
#robot .arms .right{transform-origin:0 0;transform:translateX(70%) rotate(45deg)
}
#robot .footer{width:100px;margin:auto;text-align:center;font-size:0
}
#robot .footer .foot{border:2px solid #f0f0f0;background-color:#77a2b7;border-top:0;display:inline-block;width:20px;height:10px
}
#robot .footer .foot:after{content:'';display:inline-block;width:26px;height:10px;border:1px solid #000;position:relative;left:-5px;top:10px;background-color:#000;border:2px solid #f0f0f0;border-radius:5px
}
#robot .footer .right{margin-left:10px
}
#robot .footer .left-lift:after{animation:leftlift .5s ease infinite
}
#robot .footer .right-lift:after{animation:rightlift .5s ease infinite
}
@keyframes leftlift{
0%{top:10px
}
50%{top:6px
}
100%{top:10px
}
}
@keyframes rightlift{
0%{top:6px
}
50%{top:10px
}
100%{top:6px
}
}
.copyright{position:absolute;bottom:10px;text-align:center;display:block;width:100%
}



